import { useParams } from "react-router";
import { useQuery } from "@tanstack/react-query";
import APIClient from "../../utils/ApiClient"; // your API client
import Card from "./Card"; // the same card component for items
import { imageUrl } from "../../utils/axios";
import RepeatChildren from "./../../components/RepeatChildren";
import Skeleton from "./../../components/skeleton/Skeleton";

const apiClient = new APIClient("items");

const Categories = () => {
  const { id } = useParams(); // type ID

  const { data, isLoading, isError } = useQuery({
    queryKey: ["itemsByType", id],
    queryFn: () => apiClient.getAll({ typeId: id }),
    keepPreviousData: true,
  });

  if (isError) return <p>Something went wrong.</p>;

  const items = data?.data || [];

  return (
    <div className="container flex justify-start">
      {!isLoading && items.length === 0 && (
        <p>No items found in this category.</p>
      )}
      {items.map((item) => (
        <Card
          key={item._id}
          id={item._id}
          title={item.name}
          unit={parseFloat(item.price)} // assuming price is a string in DB
          image={item.image ? `${imageUrl}${item.image}` : null}
          rating={5} // keep rating as static if you don't have it in DB
        />
      ))}

      {isLoading && (
        <RepeatChildren count={5}>
          <Skeleton
            width="150px"
            height="150px"
            style={{
              borderRadius: "50%",
              backgroundColor: "var(--body-color)",
            }}
          />
          <div
            style={{
              display: "flex",
              backgroundColor: "var(--body-color)",
              flexDirection: "column",
              gap: "10px",
              padding: "10px 15px",
              borderRadius: "10px",
              alignItems: "center",
            }}
          >
            <Skeleton
              height="20px"
              style={{
                backgroundColor: "var(--section-color)",
              }}
            />
            <Skeleton
              height="15px"
              width="50%"
              style={{ backgroundColor: "var(--section-color)" }}
            />
          </div>
        </RepeatChildren>
      )}
    </div>
  );
};

export default Categories;
